<template>
  <div class="search-container">
    <div class="search-input">
      <i class="search-icon">🔍</i>
      <input 
        :value="modelValue"
        :placeholder="placeholder"
        @input="$emit('update:modelValue', $event.target.value)"
        class="search-btn"
      >
    </div>
  </div>
</template>

<script>
export default {
  name: 'SearchInput',
  props: {
    modelValue: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: '搜索...'
    }
  },
  emits: ['update:modelValue']
}
</script>

<style scoped>
.search-container {
  margin-bottom: 2rem;
}

.search-input {
  position: relative;
  display: flex;
  align-items: center;
}

.search-icon {
  position: absolute;
  left: 1rem;
  color: white;
}

.search-btn {
  width: 100%;
  padding: 1.5rem 2.5rem 1.5rem 3.5rem;
  border-radius: 15px;
  font-size: 1.1rem;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0,0,0,0.3);
  background: rgba(255,255,255,0.2);
  backdrop-filter: blur(10px);
  border: 2px solid rgba(255,255,255,0.3);
  color: white;
}

.search-btn:focus {
  outline: none;
  background: rgba(255,255,255,0.3);
  border-color: #88fbff;
}

.search-btn::placeholder {
  color: rgba(255,255,255,0.7);
}
</style>