}

.styled-table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0 0.5rem;
}

.styled-table th {
  padding: 1.2rem 1.5rem;
  text-align: left;
  background: rgba(100, 149, 237, 0.3); /* 银蓝色表头背景 */
  color: white;
