<template>
  <div class="page-container">
    <div class="background"></div>
    <div class="content">
      <h1 class="page-title">Rating排行榜</h1>
      
      <SearchInput 
        v-model="searchQuery"
        placeholder="搜索用户..."
        @update:modelValue="handleSearch"
      />
      
      <div v-if="loading" class="skeleton-loader">
        <div v-for="i in 5" :key="i" class="skeleton-item"></div>
      </div>
      
      <div v-if="error" class="error-message">
        <i class="error-icon">⚠️</i>{{ error }}
      </div>

      <StyledTable
        v-if="!loading && !error"
        :headers="['排名', '用户名', 'Rating']"
        :data="tableData"
      />

      <div v-if="!loading && !error" class="pagination-container">
        <button 
          @click="previousPage" 
          :disabled="currentPage === 1"
          class="pagination-button prev"
        >
          ← 上一页
        </button>
        <button 
          @click="nextPage" 
          :disabled="currentPage === totalPages"
          class="pagination-button next"
        >
          下一页 →
        </button>
        
        <div class="page-indicator">
          第 {{ currentPage }} 页 / 共 {{ totalPages }} 页
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SearchInput from '@/components/SearchInput.vue';
import StyledTable from '@/components/StyledTable.vue';

export default {
  name: 'RatingRankView',
  components: {
    SearchInput,
    StyledTable
  },
  computed: {
    tableData() {
      return this.paginatedData.map(item => [
        item.rank,
        item.username,
        item.ra.toLocaleString()
      ]);
    }
  },
  data() {
    return {
      API_URL: 'https://www.diving-fish.com/api/maimaidxprober/rating_ranking',
      ITEMS_PER_PAGE: 50,  // 从20改为50
      allData: [],
      filteredData: [],
      paginatedData: [],
      searchQuery: '',
      currentPage: 1,
      totalPages: 1,
      loading: false,
      error: null
    }
  },
  async created() {
    await this.fetchData();
  },
  methods: {
    async fetchData() {
      try {
        this.loading = true;
        this.error = null;
        
        const response = await fetch(this.API_URL);
        if (!response.ok) throw new Error(`HTTP错误 ${response.status}`);
        
        const data = await response.json();
        this.allData = data
          .sort((a, b) => b.ra - a.ra)
          .map((item, index) => ({
            rank: index + 1,
            ...item
          }));
        this.filteredData = [...this.allData];
        this.totalPages = Math.ceil(this.filteredData.length / this.ITEMS_PER_PAGE);
        this.updatePaginatedData();
      } catch (error) {
        this.error = `数据加载失败: ${error.message}`;
      } finally {
        this.loading = false;
      }
    },
    handleSearch() {
      const keyword = this.searchQuery.trim().toLowerCase();
      this.filteredData = this.allData.filter(item => 
        item.username.toLowerCase().includes(keyword)
      );
      this.currentPage = 1;
      this.totalPages = Math.ceil(this.filteredData.length / this.ITEMS_PER_PAGE);
      this.updatePaginatedData();
    },
    updatePaginatedData() {
      const start = (this.currentPage - 1) * this.ITEMS_PER_PAGE;
      const end = start + this.ITEMS_PER_PAGE;
      this.paginatedData = this.filteredData.slice(start, end);
    },
    previousPage() {
      if (this.currentPage > 1) {
        this.currentPage--;
        this.updatePaginatedData();
      }
    },
    nextPage() {
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
        this.updatePaginatedData();
      }
    },
    handlePageChange() {
      this.currentPage = Math.min(Math.max(1, this.currentPage), this.totalPages);
      this.updatePaginatedData();
    }
  }
}
</script>

<style scoped>
.page-container {
  position: relative;
  min-height: 100vh;
  overflow: hidden;
}

.background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url('@/assets/bg.jpg') no-repeat center center;
  background-size: cover;
  filter: brightness(0.7);
  z-index: -1;
}

.content {
  position: relative;
  z-index: 1;
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem;
}

.pagination-button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  padding: 1rem 2rem;
  width: 100%;
  border-radius: 15px;
  font-size: 1.1rem;
  font-weight: bold;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0,0,0,0.3);
  background: rgba(255,255,255,0.2);
  backdrop-filter: blur(10px);
  border: 2px solid rgba(255,255,255,0.3);
  color: white;
}

.pagination-button:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 20px rgba(0,0,0,0.4);
  background: rgba(255,255,255,0.3);
}

.prev { color: #cbe3ff; border-color: #cbe3ff; }
.next { color: #cbe3ff; border-color: #cbe3ff; }
</style>