<template>
  <div class="page-container">
    <div class="background"></div>
    <div class="content">
      <h1>b50查询</h1>
      <SearchInput 
        v-model="qqNumber"
        placeholder="请输入QQ号"
        @update:modelValue="queryData"
      />
      
      <div v-if="loading" class="loading"></div>
      <div v-if="error" class="error">
        <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor">
          <path d="M12 8v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z"/>
        </svg>
        {{ error }}
      </div>
  
      <div v-if="playerData" class="result">
        <div class="player-info">
          <h2>{{ playerData.nickname }}</h2>
          <div class="rating-badge">总Rating: {{ playerData.rating }}</div>
        </div>
        
        <div class="song-list">
          <h3>老版本最佳成绩 ({{ playerData.charts.sd.length }})</h3>
          <div 
            v-for="chart in playerData.charts.sd" 
            :key="chart.title" 
            class="song-card"
          >
            <div class="song-details">
              <div class="song-title">{{ chart.title }}</div>
              <div class="song-meta">
                <span>{{ chart.level }}</span>
                <span>定数 {{ chart.ds }}</span>
                <span>{{ chart.rate.toUpperCase() === 'SSSP' ? 'SSS+' : chart.rate.toUpperCase() }}</span>
              </div>
            </div>
            <div class="song-rating">
              {{ chart.achievements.toFixed(2) }}%
            </div>
          </div>
          
          <h3>当前版本最佳成绩 ({{ playerData.charts.dx.length }})</h3>
          <div 
            v-for="chart in playerData.charts.dx" 
            :key="chart.title" 
            class="song-card"
          >
            <div class="song-details">
              <div class="song-title" style="text-align: center;">{{ chart.title }}</div>
              <div class="song-meta" style="display: flex; justify-content: center; align-items: center; gap: 1rem;">
                <span>{{ chart.level }}</span>
                <span>定数 {{ chart.ds }}</span>
                <span>{{ chart.rate.toUpperCase() === 'SSSP' ? 'SSS+' : chart.rate.toUpperCase() }}</span>
              </div>
            </div>
            <div class="song-rating">
              {{ chart.achievements.toFixed(2) }}%
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SearchInput from '@/components/SearchInput.vue';
export default {
  name: 'B50View',
  components: {
    SearchInput
  },
  data() {
    return {
      qqNumber: '',
      playerData: null,
      loading: false,
      error: null
    }
  },
  methods: {
    async queryData() {
      if (!this.qqNumber) return;
      
      this.loading = true;
      this.error = null;
      this.playerData = null;

      try {
        const response = await fetch('https://www.diving-fish.com/api/maimaidxprober/query/player', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({ qq: this.qqNumber, b50: true })
        });

        if (!response.ok) throw new Error(``);
        
        this.playerData = await response.json();
      } catch (error) {
        this.error = error.message;
      } finally {
        this.loading = false;
      }
    }
  }
}
</script>

<style scoped>
.page-container {
  position: relative;
  min-height: 100vh;
  padding: 20px;
  max-width: 800px;
  margin: 0 auto;
}

.background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url('@/assets/bg.jpg') no-repeat center center;
  background-size: cover;
  filter: brightness(0.7);
  z-index: -1;
}

.content {
  position: relative;
  z-index: 1;
}

.search-box input {
  border-color: #00d4ff;
}

.search-box input:focus {
  border-color: #00a8cc;
  box-shadow: 0 0 0 3px rgba(0, 212, 255, 0.2);
}

button {
  background: linear-gradient(90deg, #00d4ff, #00a8cc);
}

button:hover {
  background: linear-gradient(90deg, #00a8cc, #0082a8);
}

.rating-badge {
  background: linear-gradient(90deg, #00d4ff, #00a8cc);
}

.song-rating {
  background: linear-gradient(90deg, #00d4ff, #00a8cc);
}

.result {
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(5px);
}

.search-box {
  display: none; /* 隐藏原有的搜索框 */
  display: flex;
  gap: 1rem;
  justify-content: center;
  margin-bottom: 2rem;
}

input {
  padding: 1rem;
  border: 2px solid #ddd;
  border-radius: 0.8rem;
  width: 280px;
  font-size: 1rem;
  transition: all 0.3s ease;
}

input:focus {
  border-color: #6c5ce7;
  box-shadow: 0 0 0 3px rgba(108, 92, 231, 0.2);
  outline: none;
}

button {
  background: #6c5ce7;
  color: white;
  padding: 0.8rem 2rem;
  border: none;
  border-radius: 0.8rem;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

button:hover {
  background: #5243c4;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(108, 92, 231, 0.3);
}

.result {
  background: rgba(255, 255, 255, 0.98);
  border-radius: 1rem;
  padding: 1.5rem;
  margin-top: 1.5rem;
}

.player-info {
  text-align: center;
  margin-bottom: 2rem;
}

.player-info h2 {
  color: #6c5ce7;
  margin-bottom: 0.5rem;
}

.rating-badge {
  display: inline-block;
  background: #6c5ce7;
  color: white;
  padding: 0.5rem 1.5rem;
  border-radius: 2rem;
  font-weight: bold;
}

.song-list {
  display: grid;
  gap: 1rem;
}

.song-card {
  background: white;
  border-radius: 0.8rem;
  padding: 1.5rem;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.05);
  transition: transform 0.2s ease;
  display: grid;
  grid-template-columns: 1fr auto;
  align-items: center;
}

.song-card:hover {
  transform: translateY(-3px);
}

.song-details {
  display: grid;
  gap: 0.5rem;
}

.song-title {
  font-weight: 600;
  color: #2d3436;
  font-size: 1.1rem;
  text-align: center;  /* 新增居中样式 */
}

.song-meta {
  display: flex;
  gap: 1rem;
  color: #636e72;
  font-size: 0.9rem;
  justify-content: center;  /* 新增居中样式 */
  align-items: center;     /* 新增垂直居中 */
}

.song-rating {
  background: #a8a5e6;
  color: white;
  padding: 0.3rem 1rem;
  border-radius: 1rem;
  font-weight: bold;
}

.loading {
  display: flex;
  justify-content: center;
  padding: 2rem;
}

.loading::after {
  content: "";
  width: 2rem;
  height: 2rem;
  border: 3px solid #ddd;
  border-top-color: #6c5ce7;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

.error {
  background: #ffebee;
  color: #c62828;
  padding: 1rem;
  border-radius: 0.8rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  justify-content: center;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

@media (max-width: 600px) {
  .search-box {
    flex-direction: column;
  }
  
  input {
    width: auto;
  }
}
</style>