<template>
  <div class="upload-container">
    <div class="background"></div> <!-- Add this line if missing -->
    <div class="content">
      <h1>成绩上传</h1>
      <div class="upload-form">
        <div class="input-group">
          <label for="importToken">Import-Token:</label>
          <SearchInput
            id="importToken"
            v-model="importToken"
            placeholder="请输入您的Import-Token"
          />
        </div>
        <button @click="submitToken" :disabled="isLoading" class="btn submit">  <!-- 添加btn和submit类 -->
          {{ isLoading ? '提交中...' : '提交Token' }}
        </button>
  
        <div v-if="error" class="error-message">
          {{ error }}
        </div>
  
        <div v-if="responseData" class="result-container">
          <h2>服务器响应</h2>
          <pre>{{ JSON.stringify(responseData, null, 2) }}</pre>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SearchInput from '@/components/SearchInput.vue';
import axios from 'axios';

export default {
  name: 'UploadView',
  components: {
    SearchInput
  },
  data() {
    return {
      importToken: '',
      responseData: null,
      isLoading: false,
      error: null
    }
  },
  methods: {
    async submitToken() {
      if (!this.importToken) {
        this.error = 'Import-Token不能为空';
        return;
      }
  
      this.isLoading = true;
      this.error = null;
      this.responseData = null;
  
      try {
        const response = await axios.post(
          'https://h45a9mxjzl.hzh.sealos.run/post-data',
          {
            import_token: this.importToken
          },
          {
            headers: {
              'Content-Type': 'application/json'
            }
          }
        );
  
        this.responseData = response.data;
      } catch (err) {
        if (err.response) {
          this.error = `服务器返回错误: ${err.response.status} - ${err.response.data?.message || '无详细错误信息'}`;
        } else if (err.request) {
          this.error = '请求已发送但未收到响应，请检查网络连接';
        } else {
          this.error = '请求配置错误: ' + err.message;
        }
        console.error('完整错误信息:', err);
      } finally {
        this.isLoading = false;
      }
    }
  }
}
</script>

