<template>
  <div class="page-container">
    <div class="background"></div>
    <h1>maimai全曲</h1>
    <SearchInput 
      v-model="searchQuery"
      placeholder="输入歌曲ID或名称搜索..."
      @update:modelValue="filterSongs"
    />
    <div class="song-list">
      <div 
        v-for="song in filteredSongs" 
        :key="song.id" 
        class="song-card"
        @click="goToSongDetail(song.id)"
      >
        <h2>{{ song.title }}</h2>
        <p>艺术家: {{ song.basic_info.artist }}</p>
        <p>流派: {{ song.basic_info.genre }}</p>
        <p>BPM: {{ song.basic_info.bpm }}</p>
        <div class="difficulty">
          <span v-for="(level, index) in song.level" :key="index">
            {{ level }} ({{ song.ds[index] }})
          </span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SearchInput from '@/components/SearchInput.vue';
import songsData from '@/assets/songs.json';

export default {
  name: 'SongRankView',
  components: {
    SearchInput
  },
  data() {
    return {
      songs: songsData,
      filteredSongs: songsData,
      searchQuery: ''
    }
  },
  methods: {
    filterSongs() {
      if (!this.searchQuery) {
        this.filteredSongs = this.songs;
        return;
      }
      const query = this.searchQuery.toLowerCase();
      this.filteredSongs = this.songs.filter(song => 
        song.id.toLowerCase().includes(query) || 
        song.title.toLowerCase().includes(query)
      );
    },
    goToSongDetail(songId) {
      this.$router.push(`/song/${songId}`);
    }
  }
}
</script>

<style scoped>
.page-container {
  position: relative;
  min-height: 100vh;
  padding: 20px;
}

.background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url('@/assets/bg.jpg') no-repeat center center;
  background-size: cover;
  filter: brightness(0.7);
  z-index: -1;
}

.content {
  position: relative;
  z-index: 1;
}
.song-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  margin-top: 20px;
}
.song-card {
  background: rgba(255,255,255,0.2);
  backdrop-filter: blur(10px);
  border: 2px solid rgba(255,255,255,0.3);
  color: white;
  padding: 1.5rem;
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0,0,0,0.3);
  transition: all 0.3s ease;
  cursor: pointer;
}

.song-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 20px rgba(0,0,0,0.4);
  background: rgba(255,255,255,0.3);
}
.difficulty {
  margin-top: 10px;
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}
.search-bar {
  display: none; /* 隐藏原有的搜索框 */
}
</style>