<template>
  <div class="song-detail">
    <div class="background"></div>
    <div class="content">
      <h1>{{ song.title }}</h1>
      <div class="song-info">
        <p><strong>艺术家:</strong> {{ song.basic_info.artist }}</p>
        <p><strong>流派:</strong> {{ song.basic_info.genre }}</p>
        <p><strong>BPM:</strong> {{ song.basic_info.bpm }}</p>
      </div>
      <div class="difficulty-list">
        <h2>难度信息</h2>
        <div v-for="(level, index) in song.level" 
             :key="index" 
             class="difficulty-item"
             @click="goToDifficultyDetail(index)">
          <h3>{{ level }} ({{ song.ds[index] }})</h3>
          <p>谱师: {{ song.charts[index].charter }}</p>
          <p>Note数: {{ song.charts[index].notes.join('/') }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import songsData from '@/assets/songs.json';

export default {
  name: 'SongDetailView',
  data() {
    return {
      song: null,
      lanRecords: []
    }
  },
  methods: {
    goToDifficultyDetail(index) {
      this.$router.push(`/song/${this.$route.params.id}/difficulty/${index}`);
    }
  },
  created() {
    const songId = this.$route.params.id;
    this.song = songsData.find(song => song.id === songId);
    
   
    
  }
}
</script>

