import { createRouter, createWebHistory } from 'vue-router'
import WelcomeView from '../views/WelcomeView.vue'
import B50View from '../views/B50View.vue'
import SongRankView from '../views/SongRankView.vue'
import RatingRankView from '../views/RatingRankView.vue'
import SongDetailView from '../views/SongDetailView.vue'
import UploadView from '../views/UploadView.vue'

const routes = [
  {
    path: '/',
    redirect: '/welcome' // 修改为直接跳转到欢迎页
  },
  // 删除login和register路由
  {
    path: '/welcome',
    name: 'welcome',
    component: WelcomeView
  },
  {
    path: '/b50',
    name: 'b50',
    component: B50View
  },
  {
    path: '/song-rank',
    name: 'song-rank',
    component: SongRankView
  },
  {
    path: '/rating-rank',
    name: 'rating-rank',
    component: RatingRankView
  },
  {
    path: '/song/:id',
    name: 'song-detail',
    component: SongDetailView
  },
  {
    path: '/song/:id/difficulty/:levelIndex',
    name: 'song-difficulty-detail',
    component: () => import('../views/SongDifficultyDetailView.vue')
  },
  {
    path: '/upload',
    name: 'upload',
    component: UploadView
  }
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

export default router